import React, { useState } from 'react'
import { v4 as uuidv4 } from 'uuid'
import { Input, SideButton } from '../components'
import { Info } from '../icons'

const Test = () => {
  const [todos, setTodos] = useState([
    { _id: uuidv4(), text: '', completed: false },
  ])

  const setTodoHandler = (text, id) => {
    const newTodos = todos.map((todo) => {
      if (todo._id === id) {
        return { ...todo, text }
      }
      return todo
    })
    setTodos(newTodos)
  }

  const addNewTodoHandler = () => {
    setTodos([...todos, { _id: uuidv4(), text: '', completed: false }])
  }

  const removeTodoHandler = (todoId) => {
    setTodos(todos.filter((todo) => todo._id !== todoId))
  }

  const getTodos = () => {
    console.log({ todos })
  }
  return (
    <div className='test'>
      {todos.map((todo, idx) => (
        <div key={todo._id}>
          <Input
            name='todo'
            id={`email-${idx + 1}`}
            placeholder='add-todo'
            label='add-todo'
            type='text'
            icon={<Info />}
            custom={{ marginBottom: '3rem' }}
            value={todo.text}
            disabled={todos.length > idx + 1}
            onChange={(e) => setTodoHandler(e.target.value, todo._id)}
          />
          {todos.length === idx + 1 && (
            <SideButton text='add' handler={addNewTodoHandler} />
          )}
          {todos.length === idx + 1 && todos.length > 1 && (
            <SideButton
              text='remove'
              minus
              handler={() => removeTodoHandler(todo._id)}
            />
          )}
        </div>
      ))}

      <button onClick={getTodos}>get Todos</button>
    </div>
  )
}

export default Test
